@import "menu";
@import "form";
@import "variables";

.m { // margin
    &--l {
        margin-left: auto;
    }
    &--t {
        margin-top: auto!important;
    }
}

.w {
    &--100 {
        width: 100%;
        flex-basis: 100%;
    }
    &--50 {
        width: 50%;
        flex-basis: 50%;
    }
    &--30 {
        width: 30%;
        flex-basis: 30%;
    }
    &--70 {
        width: 70%;
        flex-basis: 70%;
    }

    &--40 {
        width: 40%;
        flex-basis: 40%;
    }
    &--60 {
        width: 60%;
        flex-basis: 60%;
    }
    &--80 {
        width: 80%;
        flex-basis: 80%;
    }
    
}
.bg {
    &--white {
        background: $white;
    }
    &--gray {
        background-color: $lighten-gray;
    }
}

.layer {
    &--bg {
        z-index: 1;
    }
    &--mid {
        z-index: 50;
    }
    &--top {
        z-index: 999;
    }
}

.align {
    &--center {
        text-align: center;
        justify-content: center;
    }
    &--right {
        text-align: right;
        justify-content: right;
    }
}

.image {
    &--responsive {
        width: 100%;
        height: auto;
    }
    &--responsive-vertical {
        height: 100%;
        width: auto;
    }
}

.list {
    &--circle {
        list-style-type: circle;
        list-style-position: inside;
    }
}
.list--number {
    list-style: none;
    li {
        margin-bottom: 1em;
        padding-left: 2.5em;
        position: relative;
        h3 {
            &::before {
                content: "0"counter(list-item)"\a0\a0";
                font-weight: 500;
                position: absolute;
                left: 0;
                top: 0;
            }
        }
    }
}

.dialog {
    .MuiDialogTitle-root + .MuiDialogContent-root {
        padding-top: 24px;
    }
    .MuiDialog {
        &Title-root {
            background-color: $black;
            color: $white;
            text-align: center;
            margin-bottom: 1em;
            position: relative;
            font-weight: normal;
            .MuiIconButton-root {
                font-size: 20px;
                font-weight: normal;
                color: $white;
                position: absolute;
                right: 24px;
                top: 50%;
                transform: translate(12px, -50%);
            }
        }
        &-paper {
            width: 600px;
        }
        &Actions {
            &-root {
                padding-right: 24px;
                padding-left: 24px;
            }
        }
        &-paperFullScreen {
            .MuiDialogTitle-root button svg {
                font-size: 20px;
            }
        }
        &Content-root {
            h3 {
                font-weight: normal;
            }
        }
    }
    @media screen and (max-width: $mobile-break) {
        .MuiDialog {
            &-paper {
                min-width: 100vw;
            }
        }
    }
}

.icon {
    margin-right: 0.618em;
    display: inline-block;
    width: 1.618em;
    height: 1.618em;
    mask-repeat: no-repeat;
    mask-size: contain;
    mask-position: center;
    background-color: $black;
    &--faqs {
        mask-image: url('../../assets/icons/icon-faqs.svg');
