@import '../../assets/scss/variables';
@import '../../assets/scss/flex';

.wallet {
    .page {
        position: relative;
        @extend .flex;
        @extend .flex--vertical;
        &__actions {
            position: absolute;
            right: 0;
            top: calc(35px);
            button {
                margin-bottom: 8px;
                margin-left: 8px;
            }
        }
        &__title {
            margin-bottom: 0.618em;
        }
    }
    .tokens {
        margin-top: -24px;
    }
}


@media screen and (max-width: 560px) {
    .wallet {

        .page {
            &__title {
                margin-bottom: 8px;
            }
            &__actions {
                position: relative;
                top: 0;
            }
        }
        .tokens {
            &__list {
                padding-left: 12px;
                padding-right: 12px;
            }
            &--list-view {
                .token {
                    td {
                        padding-left: 0.5em!important;
                        padding-right: 0.5em!important;
                        padding-top: 0.125em;
                        padding-bottom: 0.125em;
                        align-items: center;
                        &::before {
                            flex-basis: 50%;
                        }
                    }
                    &__thumbnail {
                        position: relative;
                        width: 50px;
                        left: auto;
                        top: auto;
                    }
                }
            }
        }
    }
}