@import '../../assets/scss/variables';

.gallery {
	width: 100%;
    &__item {
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 100%;
        height: 400px;
    }
    .carousel {
        .slide {
            position: relative;
            overflow: hidden;
            > span {
                background-size: contain;
                background-repeat: no-repeat;
            }
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                height: 100%;
                width: auto;
                transform: translate(-50%, -50%);
            }
        }
        .thumbs-wrapper {
            margin-left: 0;
            margin-right: 0;
            .thumbs {
                display: flex;
                align-items: center;
                justify-content: center;
                .thumb {
                    flex: 1;
                    overflow: hidden;
                    position: relative;
                    border: 1px solid $gray;
                    cursor: pointer;
                    max-width: calc(100% / 4);
                    &.selected {
                        border-width: 1px;
                        border-color: $black;
                    }
                    &:after {
                        content: "";
                        display: block;
                        padding-top: 100%;
                    }
                    img {
                        position: absolute;
                        display: block!important;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        width: 100%;
                        height: auto;
                    }
                }
            }
        }
    }
}