@import '../../assets/scss/variables';

.site__transactions .transactions__filter .transaction__date-range {
    flex-grow: 1;
}
.rdrCalendarWrapper {
    background-color: transparent;
    width: 100%;
    .rdrMonth {
        width: 100%;
    }
    .rdrDateDisplayWrapper {
        background-color: transparent;
        padding: 0;
        margin-bottom: 1em;
    }
    .rdrMonthAndYearWrapper {
        background-color: $white;
    }
    .rdrDateDisplay {
        margin: 0;
        .rdrDateInput {
            border-radius: 0;
            background-color: transparent;
            box-shadow: none;
            display: flex;
            align-items: center;
            flex-shrink: 0;
            margin-left: 0;
            input {
                background-color: $white;
            }
            &.rdrDateDisplayItemActive {
                border: none;
                input {
                    border: 1px solid $red;
                }
            }
            &:first-child {
                flex-basis: calc(50% - 1em);
                &::after {
                    content: " - ";
                    display: block;
                    right: 0;
                    margin-left: 1em;
                    margin-right: 1em;
                    color: $black;
                }
            }
            &:last-child {
                flex-basis: calc(50% - 3em);
            }
        }
    }
    .rdrMonths {
        background-color: $white;
    }
}