@import "../../assets/scss/variables";

.site {
    &.token--public {
        .platform__toolbar {
            background-color: $black;
        }
        .site--platform {
            padding-left: 0;
            padding-right: 0;
            background-color: $white;
        }
        .page {
            padding-top: 0;
            width: 100%;
            margin: 0 auto;
            max-width: 1440px;
            background: $white;
            &__title {
                width: 100vw;
                padding: 0 40px;
                position: relative;
                left: 50%;
                transform: translate(-50%, 0);
                text-align: center;
                padding-top: 35px;
                padding-bottom: 35px;
                margin-bottom: 0;
                min-height: 300px;
                background-color: $lighten-gray;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                font-size: 56px;
                line-height: 1em;
                word-wrap: break-word;
                word-wrap: anywhere;
                &::after {
                    content: none;  
                }
                margin-bottom: 32px;
            }
            &__content {
                padding-left: 95px;
                padding-right: 95px;
                .MuiPaper-root {
                    border-radius: 0;
                }
                .MuiTypography-body2 {
                    font-size: 18px;
                }
                h3 {
                    font-size: 28px;
                    line-height: 1.618em;
                }
            }
            .token {
                h3 {
                    margin-bottom: 8px;
                }
                .gallery {
                    background-color: $lighten-gray;
                    &__item {
                        background-color: $white;
                    }
                }
                &__description {
                    margin-bottom: 1em;
                }
                &__specifications {
                    flex-wrap: wrap;
                    p {
                        display: flex;
                        flex-wrap: wrap;
                        width: 100%;
                        > span {
                            flex-basis: 30%;
                            width: 30%;
                            min-width: 200px;
                        }
                    }
                }
                &__transactions {
                    padding: 0!important;
                    width: 100%;
                }
                &__documents {
                    width: 100%;
                }
                .transaction {
                    &__wrapper {
                        border-bottom: 1px solid $lighten-gray;
                        margin-bottom: 24px;
                    }
                    &__type {
                        width: 100%;
                        display: block;
                        font-weight: normal;
                        text-transform: none;
                        display: flex;
                        .label {
                            display: inline-block;
                            flex-basis: 30%;
                            max-width: 150px;
                            min-width: 150px;
                        }
                    }
                    &__details {
                        width: 100%;
                        padding-left: 0;
                        padding-right: 0;
                        padding-top: 12px;
                        padding-bottom: 12px;
                        .MuiCollapse-wrapperInner > .flex {
                            flex-direction: column!important;
                        }
                    }
                    &__detail {
                        width: 100%;
                        flex-direction: row;
                        white-space: pre-wrap;
                        > span {
                            flex-basis: 30%;
                            max-width: 150px;
                            min-width: 150px;
                        }
                        p {
                            word-wrap: anywhere;
                        }
                    }
                }
            }
            .token__document {
                padding: 24px 0;
                border-bottom: 1px solid $darken-gray;
                .document {
                    &__thumbnail {
                        width: 70px;
                        height: 70px;
                        background-color: darken($color: $white, $amount: 10%);
                        background-size: contain;
                        background-position: center;
                        background-repeat: no-repeat;
                    }
                    &__informations {
                        p span.label {
                            flex-basis: 30%;
                            width: 30%;
                            min-width: 150px;
                            display: inline-block;
                        }
                    }
                }
                &-type:last-child {
                    .token__document:last-child {
                        border: none;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $mobile-break) {
    .site.token--public {
        .page__content {
            padding: 0;
            max-width: calc(100% - 80px);
            margin: 0 auto;
            .token {
                > .flex {
                    flex-direction: column!important;
                    [class^="w--"] {
                        width: 100%;
                        flex-basis: 100%;
                        padding: 0;
                    }
                }
            }
        }
    } 
}