@import '../../../assets/scss/variables';
@import '../../../assets/scss/flex';

.rdrCalendarWrapper {
    background-color: transparent;
    width: 100%;
    .rdrMonth {
        width: 100%;
    }
    .rdrDateDisplayWrapper {
        background-color: transparent;
        padding: 0;
        margin-bottom: 1em;
    }
    .rdrMonthAndYearWrapper {
        background-color: $white;
    }
    .rdrDateDisplay {
        margin: 0;
        .rdrDateInput {
            border-radius: 0;
            background-color: transparent;
            box-shadow: none;
            display: flex;
            align-items: center;
            flex-shrink: 0;
            margin-left: 0;
            input {
                background-color: $white;
            }
            &.rdrDateDisplayItemActive {
                border: none;
                input {
                    border: 1px solid $red;
                }
            }
            &:first-child {
                flex-basis: calc(50% - 1em);
                &::after {
                    content: " - ";
                    display: block;
                    right: 0;
                    margin-left: 1em;
                    margin-right: 1em;
                    color: $black;
                }
            }
            &:last-child {
                flex-basis: calc(50% - 3em);
            }
        }
    }
    .rdrMonths {
        background-color: $white;
    }
}
.tokens {
    &__list {
        background-color: $white;
        padding: 24px;
        overflow-x: scroll;
    }
    &__actions {
        position: relative;
        flex-wrap: wrap!important;
    }
    &__filters {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        flex-grow: 1;
        flex-basis: 50%;
    }
    .filter {
        font-size: 14px;
        line-height: 1em;
        background-color: $white;
        flex-shrink: 0;
        flex-grow: 0;
        .MuiInputBase-root {
            padding: 0;
            margin-left: 0;
        }
        input {
            height: 1em;
            padding: calc( ( 30px - 1em ) / 2 );
            width: 0;
            padding-left: 0;
            padding-right: 0;
            transition: width ease-in-out 300ms;
        }
        button {
            font-size: 0.6182em;
            height: 30px;
            width: 30px;
            color: $black;
            background-color: $white;
            border-radius: 2px;
            padding: calc((30px - 14px) / 2);
            overflow: hidden;
        }
        &.open {
            input {
                width: 100%;
                padding-left: 8px;
                padding-right: 8px;
            }
        }
        .MuiFormControl-root {
            flex-shrink: 0;
            flex-grow: 0;
        }
        &__by-status {
            order: 1;
            &--details {
                margin-top: 32px;
                order: 3;
                flex-shrink: 0;
                flex-grow: 1;
                flex-basis: 100%;
                &.MuiCollapse-entered {
                    // padding: 1em 8px;
                    clear: both;
                }
                
                .MuiCollapse-wrapperInner {
                    @extend .flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    background-color: #C8CDCF;
                    padding: 1em;
                    .label { 
                        flex-basis: 20ch;
                    } 
                }
                .MuiFormControl-root {
                    margin-right: 2em;
                    .MuiFormGroup-root {
                        flex-direction: row;
                    }
                }
                @media screen and (max-width: 767px ) {
                    flex-direction: column;
                    align-items: center;
                    .MuiCollapse-wrapperInner {
                        align-items: flex-start;
                        justify-content: flex-start;
                    }
                    .MuiFormControl-root {
                        .MuiFormGroup-root {
                            flex-direction: column;
                        }
                    }
                    
                    
                    &.MuiCollapse-entered {
                        &::after {
                            // content: none;
                        }
                    }
                }
            }
        }
        &__by-name {
            order: 2;
            flex-shrink: 1;
            flex-grow: 0;
            max-width: calc(100% - 60px);
        }
    }

    &__view-switcher {
        &-inner {
            border: 1px solid $black;
            border-radius: 4px;
            display: flex;
        }
        button {
            background-color: #E5E7E8;
            color: $black;
            text-transform: inherit;
            padding-left: 1em;
            padding-right: 1em;
            padding-top: 0.618em;
            padding-bottom: 0.618em;
            line-height: 1em;
            &:first-child {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                border-right: 1px solid $black;
            }
            &:last-child {
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
            }
            &.active {
                background-color: #C9CED0;
            }
        }
    }
    .token {
        cursor: pointer;
        &__thumbnail {
            background-size: cover;
            background-repeat: no-repeat;
            // &:hover .token__favorite {
            //     display: block!important;
            // }
        }
        &__favorite {
            position: absolute;
            display: none;
            font-size: 14px;    
            color: lighten($color: $red, $amount: 20%);
            top: 0;
            left: 0;
            &.favorited,
            &:hover {
                color: $red;
            }
        }


        &__status {
            text-transform: capitalize;
            &--incoming {
                color: lighten($red, 10%);
            }
            &--managed,
            &--transferring {
                color: $darken-gray;
            }

            &--declined,
            &--refused {
                color: #F47876;
            }
            &--owned,
            &--accepted {
                color: #ACD794;
            }
        }
        &__badges {
            .MuiChip {
                &-root {
                    background-color: $white;
                    box-shadow: 1px 1px 1px 0px transparentize($color: $darken-gray, $amount: 0.3);
                    border-radius: 2px;
                    width: 100%;
                    max-width: 20ch;
                    font-size: 10px;
                    &.token__operator-status {
                        &--managed,
                        &--transering {
                            background-color: $darken-gray;
                        }
                        &--refused {
                            background-color: #F47876;
                        }
                        &--owned,
                        &--accepted {
                            background-color: #ACD794;
                        }
                    }
                }
                &-label {
                    text-align: center;
                    font-size: 12px;
                }
            }
        }
    }
    &--grid-view {
        display: flex;
        flex-wrap: wrap;
        .token {
            font-size: 14px;
            line-height: 24px;
            flex-basis: calc((100% - 20px*3)/4);
            width: calc((100% - 20px*3)/4);
            background-color: $lighten-gray;
            margin-top: 0;
            margin-bottom: 1rem;
            margin-right: 20px;
            padding: 1em;
            &:nth-child(4n) {
                margin-right: 0;
            }
            @media screen and (max-width: 994px) and (min-width: 768px) {
                flex-basis: calc((100% - 20px)/2);
                width: calc((100% - 20px)/2);
                &:nth-child(2n) {
                    margin-right: 0;
                }
            }
            @media screen and (max-width: 767px) {
                flex-basis: 100%;
                width: 100%;
                margin-right: 0;
            }

            .Mui {
                &Card {
                    &Header {
                        &-root {
                            padding-bottom: 0;
                            padding-top: 0;
                            padding-left: 0;
                            padding-right: 0;
                            margin-bottom: 1rem;
                        }
                        &-title {
                            font-size: inherit;
                            line-height: inherit;
                        }
                    }
                    &Media {
                        &-root {
                            margin-bottom: 1rem;
                            aspect-ratio: 2/1;
                            overflow: hidden;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            background-color: $white;
                            border-radius: 4px;
                            position: relative;
                            img {
                                height: 100%;
                                width: auto;
                            }
                        }
                    }
                    &Actions {
                        &-root {
                            padding-left: 0;
                            padding-right: 0;
                            padding-bottom: 0;
                            align-items: right;
                            justify-content: flex-end;
                            display: flex;
                        }
                    }
                }
            }
            &__description {
                color: $text-gray;
                height: calc(24px * 4);
                overflow: hidden;
                text-overflow: ellipsis;
            }
            &__detail {
                font-weight: $black;
                border-top: 1px solid $darken-gray;
                font-size: 14px;
                padding: 8px 0;
            }
            &__badges {
                position: absolute;
                right: 4px;
                top: 4px;
            }
            &__link {
                font-size: 12px;
                color: $black;
                background-color: $white;
            }
        }
    }
    &--list-view {
        .MuiTableHead-root tr th {
            padding: 24px 0;
        }
        @media screen and (max-width: 900px) {
            
            .placeholder {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                td {
                    flex-basis: auto!important;
                    flex-grow: 1!important;
                    .MuiSkeleton-root  {
                        width: 100%;
                    }
                    .image {
                        position: absolute;
                        left: 1em;
                        top: 1em;
                        width: 100px!important;
                    }
                }
            }
        }
        .token {
            &:nth-child(2n - 1) {
                td {
                    background-color: $lighten-gray;
                }
            }
            .MuiTable {
                &Cell {
                    &-root {
                        padding: 0 8px 0 0;
                        &::before {
                            display: none;
                        }
                    }
                }
            }
            &__thumbnail {
                width: 60px;
                min-width: 60px;
                position: relative;
                overflow: hidden;
                margin-right: 0.618em;
                &::after {
                    content: " ";
                    display: block;
                    padding-top: 100%;
                }
                img {
                    position: absolute;
                    width: 100%;
                    height: auto;
                    left: 50%;
                    top: 0;
                    transform: translate(-50%, 0);
                }
            }
            &__link {
                font-size: 12px;
                color: $black;
            }
            @media screen and (max-width: 900px) {
                overflow: hidden;
                width: 100%;
                td {
                    position: initial;
                    padding-left: calc(100px + 2em)!important;
                    line-height: 1.618em;
                    flex-basis: 100%!important;
                    > div {
                        position: initial;
                        flex-direction: column-reverse!important;
                        align-items: flex-start;
                    }
                    &:first-child {
                        padding-top: 1em!important;
                    }
                    &:last-child {
                        padding-bottom: 1em!important;
                    }
                }
                &__thumbnail {
                    position: absolute;
                    left: 1em;
                    top: 1em;
                    width: 100px;
                }
                .token__link {
                    margin-left: calc(30%);
                    padding-top: 1em;
                }
            }
        }
    }
    &--empty {
        .wrapper {
            max-width: 400px;
            padding: 2em 0;
        }
        .tokens__list {
            .icon {
                width: 30vw;
                height: 30vw;
                max-width: 400px;
                max-height: 400px;
                -webkit-mask-image: none;
                mask-image: none;
                background-image: url('../../../assets/icons/icon-wallet-empty.svg');
                background-color: transparent;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }
}